.modal {
  display: block !important;
  visibility: visible !important;
  height: auto; /* Adjust height automatically based on content */
  width: 650px;
  margin: 40px auto; /* Center horizontally with some top margin */
  box-shadow: 0 15px 35px rgba(0, 0, 0, .5);
  position: relative; /* Ensure it is positioned within the flow of the page */
  font-size: 0;
  background: linear-gradient(-45deg, #001F60, #009DE0);
}

.modal .side {
  background: linear-gradient(-45deg, #001F60, #009DE0);
  height: 100%;
  width: 100%;
  position: relative;
}

.modal .side .skew {
  background: #F6F6F6;
  height: 110%;
  width: 45%;
  padding: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, .25);
  position: relative;
  z-index: 5;
  transform: skew(8deg);
}

.modal .side .skew * {
  transform: skew(-8deg);
}

.modal .side .skew h3 {
  color: #474747;
  font-size: 25px;
  text-align: center;
}

.modal .side .skew img {
  width: 100%;
  margin: 25px 0;
}

.modal .side .skew .btn {
  background: #474747;
  width: 180px;
  margin: 10px auto;
  padding: 10px 18px;
  font-size: 16px;
  text-align: center;
  border-radius: 3px;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, .25);
  cursor: default;
}

/* Header Styles */
.modal .header {
  width: 60%;
  margin: 20px auto; /* Center header */
}

.modal .header h1 {
  background: linear-gradient(-45deg, #2ed6bc, #01BCE6);
  padding: 10px 25px;
  font-size: 25px;
  color: white;

}

/* Form Styles */
.modal .form {
  width: 100%;
  max-width: 400px; /* Set a max-width for better responsiveness */
  margin: 20px auto;
}

.modal .form input,
.modal .form select,
.modal .form fieldset,
.modal .form button {
  border: none;
  outline: none;
  font-size: 16px;
}

.modal .form fieldset {
  display: block;
}

.modal .form label {
  display: block;
  color: #01BCE6;
  font-weight: 500;
  font-size: 13px;
}

.modal .form input,
.modal .form select {
  background: rgba(255, 255, 255, 0.18);
  padding: 8px 12px;
  margin: 5px 5px 5px 0;
  border-radius: 3px;
  color: #FFF;
}

.modal .form input:last-of-type,
.modal .form select:last-of-type {
  margin-right: 0;
}

.modal .form select option {
  background: #474747;
}

.modal .form .number input {
  width: calc(25% - 7px);
}

.modal .form .name input {
  width: 100%;
}

.modal .form .exp,
.modal .form .ccv {
  display: inline-block;
}

.modal .form .exp {
  width: 60%;
}

.modal .form .exp select {
  width: calc(50% - 5px);
}

.modal .form .ccv {
  width: 40%;
}

.modal .form .ccv input {
  width: 100%;
}

.modal .form button {
  display: block;
  background: linear-gradient(-45deg, #2ed6bc, #01BCE6);
  width: 200px;
  color: white;
  padding: 10px 18px;
  margin: 40px auto;
  font-size: 18px;
  border-radius: 22px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
  cursor: pointer;
}

@media (max-width: 670px){
  .modal {
    display: block !important;
    visibility: visible !important;
    height: auto;
    width: 450px;
    margin: 40px auto;
    box-shadow: 0 15px 35px rgba(0, 0, 0, .5);
    position: relative;
    font-size: 0;
    background: linear-gradient(-45deg, #001F60, #009DE0);
  }
}